import {
  ArgumentsHost,
  Catch,
  ExceptionFilter,
  HttpException,
  HttpStatus,
  Logger,
  UnauthorizedException,
} from '@nestjs/common'
import { CustomError } from '@flowda-projects/flowda-shared'
import { ZodValidationException } from 'nestjs-zod'

@Catch()
export class AppExceptionFilter implements ExceptionFilter {
  private readonly logger = new Logger(AppExceptionFilter.name)

  catch(exception: unknown, host: ArgumentsHost) {
    const ctx = host.switchToHttp()
    const response = ctx.getResponse()

    let code
    let message
    let status
    let stack

    // 如果是 CustomError 提取 errorCode + message, 200
    if (exception instanceof CustomError) {
      const rt = JSON.parse(exception.message)
      code = rt.errorCode
      message = rt.message
      status = HttpStatus.OK
    } else if (exception instanceof Error) {
      // 如果是一般 Error，提取 message，errorCode 继续 undef
      message = exception.message
      status = HttpStatus.INTERNAL_SERVER_ERROR
      stack = exception.stack
    }

    // 如果是 HttpException，则重新赋值下 status
    if (exception instanceof HttpException) {
      status = exception.getStatus()
      code = status
      const res = exception.getResponse()
      if (typeof res === 'object' && Array.isArray((res as Record<string, any>).message)) {
        message = (res as unknown as { message: string[] }).message.join(',')
      }
    }

    if (exception instanceof UnauthorizedException) {
      code = exception.getStatus()
      message = exception.message
    }

    if (exception instanceof ZodValidationException) {
      code = exception.getStatus()
      message = exception.message
    }

    this.logger.error({
      code: code,
      message: message,
      stack: stack,
    })

    response.status(status).json({
      code: code,
      message: message,
    })
  }
}
