import { Body, Controller, Delete, Get, HttpCode, Post, Put, Req } from '@nestjs/common'
import { DataService } from '../services/data/data.service'

@Controller('data/*')
export class DataController {
  constructor(private service: DataService) {}

  @Get('')
  async get(@Req() req: any) {
    return this.service.get(req.params[0], req.query)
  }

  @Put('')
  async put(@Req() req: any, @Body() values: any) {
    return this.service.put(req.params[0], values)
  }

  @Post('')
  async post(@Req() req: any, @Body() values: any) {
    return this.service.post(req.params[0], values)
  }

  @Delete('')
  @HttpCode(200)
  async remove(@Req() req: any) {
    return this.service.remove(req.params[0])
  }
}
