/* eslint-disable @typescript-eslint/no-namespace */
import { CustomError } from '@flowda-projects/flowda-shared'

export namespace UserError {
  export class UserExist extends CustomError {
    constructor() {
      super(1001, 'User exist')
    }
  }
  export class UserNotExist extends CustomError {
    constructor() {
      super(1002, 'User not exist')
    }
  }
  export class UserNamePasswordMismatch extends CustomError {
    constructor() {
      super(1002, 'Username and password mismatch')
    }
  }
}
