import { MemberInfoSchema, ResourceTaskDefKeyRelationSchema, UserSchema } from '@flowda-projects/prisma-wms'
import { meta } from '@flowda-projects/flowda-engine'
import { z } from 'zod'

export const MemberInfoResourceSchema = MemberInfoSchema.omit({
  isDeleted: true,
}).extend({
  // motor-admin schema
  __meta: meta({
    extends: 'MemberInfoSchema',
  }),
})

export const UserResourceSchema = UserSchema.omit({
  isDeleted: true,
  hashedPassword: true,
  hashedRefreshToken: true,
}).extend({
  // motor-admin schema
  __meta: meta({
    extends: 'UserSchema',
  }),
})

export const TaskResourceSchema = z
  .object({
    id: z.string().cuid(),
    name: z.string(),
    assignee: z.string(),
    created: z.date(),
    executionId: z.string(),
    processDefinitionId: z.string(),
    processInstanceId: z.string(),
    taskDefinitionKey: z.string(),
    variables: z.string(),
  })
  .extend({
    // motor-admin schema
    __meta: meta({
      prisma: false,
    }),
  })
  .openapi({
    primary_key: 'id',
    display_name: '任务',
    display_column: 'name',
    display_primary_key: 'true',
  })

export const ResourceTaskDefKeyRelationResourceSchema = ResourceTaskDefKeyRelationSchema.omit({
  isDeleted: true,
}).extend({
  // motor-admin schema
  __meta: meta({
    extends: 'ResourceTaskDefKeyRelationSchema',
  }),
})

export const ProcessDefinitionResourceSchema = z
  .object({
    id: z.string(),
    key: z.string(),
    name: z.string(),
    version: z.number(),
    resource: z.string(),
    deploymentId: z.string().cuid(),
    suspended: z.boolean(),
    historyTimeToLive: z.number(),
    startableInTasklist: z.boolean(),
  })
  .extend({
    // motor-admin schema
    __meta: meta({
      prisma: false,
    }),
  })
  .openapi({
    primary_key: 'id',
    display_name: '已部署流程图',
    display_column: 'name',
    display_primary_key: 'true',
  })
