import { inject, injectable } from 'inversify'
import { matchPath, PrismaClientSymbol, toModelName } from '@flowda-projects/flowda-shared'
import * as _ from 'lodash'
import { LoggerService } from '@nestjs/common'

/*
todo: 增加 reference_type 区分是如何做 nest
e.g. Customer#weixinProfile 和 Order#customerId 的 nest 查询有区别
 */
@injectable()
export class DataService {
  private readonly logger

  constructor(
    @inject(PrismaClientSymbol) private prisma: any,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(DataService.name)
  }

  private toPrismaSelect(fields: string) {
    return fields.split(',').reduce((acc: Record<string, boolean>, cur: string) => {
      acc[cur] = true
      return acc
    }, {})
  }

  async get(pathname: string, query: Record<string, any>) {
    const ret = matchPath(pathname)
    if (ret.length === 0) {
      return {}
    } else {
      const { resource, id } = ret[ret.length - 1]
      const fields = this.toPrismaSelect(query.fields[resource])

      const include: Record<string, any> = {}
      if (!_.isEmpty(query.include)) {
        query.include.split(',').forEach((inc: string) => {
          include[inc] = {
            select: this.toPrismaSelect(query.fields[inc]),
          }
        })
      }

      // 情况1：搜索单个
      if (id != null) {
        const nid = await this.parseId(resource, id)
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        return this.prisma[resource].findUnique({
          where: {
            id: nid,
          },
          select: fields,
        })
      } else {
        if (ret.length > 1) {
          // 情况1：根据前一个 resource id 搜索 list
          const pResource = ret[ret.length - 2]
          this.logger.log(`${resource}.findMany`)
          // eslint-disable-next-line @typescript-eslint/ban-ts-comment
          // @ts-ignore
          return this.prisma[resource].findMany({
            where: {
              [`${pResource.resource}Id`]: pResource.id,
              isDeleted: false,
            },
            select: { ...fields, ...include },
          })
        } else {
          this.logger.log(`${resource}.findMany`)
          // eslint-disable-next-line @typescript-eslint/ban-ts-comment
          // @ts-ignore
          return this.prisma[resource].findMany({
            where: {
              // todo: tenantId
              isDeleted: false,
            },
            select: fields,
          })
        }
      }
    }
  }

  private async parseId(resource: string, id: string) {
    const modelName = toModelName(resource)
    const dmmf = await this.prisma._getDmmf()
    const idField = dmmf.modelMap[modelName].fields.find((item: any) => item.name === 'id')
    const nId = idField.type === 'Int' ? parseInt(id) : id
    this.logger.log('id field:' + idField.type)
    return nId
  }

  async put(path: string, values: any) {
    const ret = matchPath(path)
    const { resource, id } = ret[ret.length - 1]
    const nid = await this.parseId(resource, id as string)
    values.id = nid

    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    return this.prisma[resource].update({
      where: { id: nid },
      data: values,
    })
  }

  async post(path: string, values: any) {
    const ret = matchPath(path)
    const { resource } = ret[ret.length - 1]
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    return this.prisma[resource].create({
      data: values,
    })
  }

  async remove(path: string) {
    const ret = matchPath(path)
    const { resource, id } = ret[ret.length - 1]
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    return this.prisma[resource].update({
      where: {
        id,
      },
      data: {
        isDeleted: true,
      },
    })
  }
}
