import { Controller, Get } from '@nestjs/common'

import { SchemaService } from '@flowda-projects/flowda-engine'
import { PostService } from '@flowda-projects/wms-services'

@Controller('/apps')
export class AppController {
  constructor(private readonly schema: SchemaService, private readonly post: PostService) {}

  @Get('/__hi')
  hi() {
    return { hi: 'world' }
  }

  @Get('/getSchema')
  getSchema() {
    return this.schema.getSchema()
  }

  @Get('/getPosts')
  getPosts() {
    return this.post.findAll()
  }
}
